"use client"

import jsPDF from "jspdf"
import html2canvas from "html2canvas"
import React from "react"
import { createRoot } from "react-dom/client"
import { InvoiceTemplate } from "@/components/invoice-template"

interface InvoiceData {
  id: string
  company: string
  amount: number
  contactName: string
  contactEmail: string
}

export async function generateInvoicePDF(records: InvoiceData[]) {
  const pdf = new jsPDF({
    orientation: "portrait",
    unit: "mm",
    format: "a4",
  })

  const pdfWidth = pdf.internal.pageSize.getWidth()
  const pdfHeight = pdf.internal.pageSize.getHeight()

  // Create a temporary container for invoices
  const tempContainer = document.createElement("div")
  tempContainer.style.position = "fixed"
  tempContainer.style.left = "0"
  tempContainer.style.top = "0"
  tempContainer.style.width = "210mm"
  tempContainer.style.zIndex = "9999"
  tempContainer.style.backgroundColor = "#ffffff"
  document.body.appendChild(tempContainer)

  try {
    // Render all invoices to the temporary container
    const root = createRoot(tempContainer)

    await new Promise<void>((resolve) => {
      root.render(
        <div>
          {records.map((record, index) => (
            <InvoiceTemplate
              key={record.id}
              record={record}
              pageNumber={index + 1}
              totalPages={records.length}
            />
          ))}
        </div>
      )
      // Wait for React 19's concurrent rendering
      setTimeout(resolve, 1500)
    })

    console.log("Invoices rendered, starting PDF generation...")

    // Generate PDF for each invoice
    for (let index = 0; index < records.length; index++) {
      if (index > 0) {
        pdf.addPage()
      }

      const record = records[index]
      const invoiceElement = document.getElementById(`invoice-${record.id}`)

      if (!invoiceElement) {
        console.error(`Invoice element not found for record ${record.id}`)
        console.log("Available elements:", document.querySelectorAll('[id^="invoice-"]'))
        continue
      }

      console.log(`Generating page ${index + 1} for ${record.company}...`)

      try {
        const canvas = await html2canvas(invoiceElement, {
          scale: 2,
          useCORS: true,
          logging: true,
          backgroundColor: "#ffffff",
          width: invoiceElement.offsetWidth,
          height: invoiceElement.offsetHeight,
          windowWidth: invoiceElement.scrollWidth,
          windowHeight: invoiceElement.scrollHeight,
        })

        console.log(`Canvas created: ${canvas.width}x${canvas.height}`)

        const imgData = canvas.toDataURL("image/png")
        const imgWidth = pdfWidth
        const imgHeight = (canvas.height * pdfWidth) / canvas.width

        pdf.addImage(imgData, "PNG", 0, 0, imgWidth, imgHeight)

        console.log(`Added image to PDF: ${imgWidth}mm x ${imgHeight}mm`)
      } catch (error) {
        console.error("Error generating PDF page:", error)
      }
    }

    // Clean up
    root.unmount()
    document.body.removeChild(tempContainer)
  } catch (error) {
    console.error("Error in PDF generation:", error)
    // Clean up on error
    if (tempContainer.parentNode) {
      document.body.removeChild(tempContainer)
    }
  }

  const fileName = `invoices_${new Date().toISOString().split("T")[0]}.pdf`
  pdf.save(fileName)
}
