"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Checkbox } from "@/components/ui/checkbox"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { FileText, Download, Eye } from "lucide-react"
import { generateInvoicePDF } from "@/lib/pdf-generator"
import Image from "next/image"
import { InvoicePreviewDialog } from "@/components/invoice-preview-dialog"

interface Record {
  id: string
  company: string
  amount: number
  contactName: string
  contactEmail: string
}

export default function InvoicePage() {
  const [records, setRecords] = useState<Record[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [selectedRecords, setSelectedRecords] = useState<string[]>([])
  const [isGenerating, setIsGenerating] = useState(false)
  const [previewOpen, setPreviewOpen] = useState(false)
  const [previewRecords, setPreviewRecords] = useState<Record[]>([])

  useEffect(() => {
    const fetchRecords = async () => {
      try {
        console.log("Fetching records from API...")
        const response = await fetch("/api/records")

        if (!response.ok) {
          const errorData = await response.json()
          console.error("API error:", errorData)
          throw new Error(errorData.error || "Failed to fetch records")
        }

        const data = await response.json()
        console.log("Received data:", data)
        console.log("Data type:", Array.isArray(data) ? "Array" : typeof data)
        console.log("Data length:", Array.isArray(data) ? data.length : "N/A")

        let transformedData: Record[] = []

        if (Array.isArray(data)) {
          transformedData = data.map((item: any) => ({
            id: item.id || String(Math.random()),
            company: item.company || item.회사명 || "",
            amount: typeof item.amount === "number" ? item.amount : Number.parseFloat(item.amount) || 0,
            contactName: item.contactName || item.담당자 || "",
            contactEmail: item.contactEmail || item["담당자 이메일"] || "",
          }))
        } else if (data.data && data.data.outputs) {
          // MISO 원본 형식 처리
          const outputs = data.data.outputs
          transformedData = [
            {
              id: "1",
              company: outputs["회사명"] || "",
              amount: Number.parseFloat(outputs["최종금액"]) || 0,
              contactName: outputs["담당자"] || "",
              contactEmail: outputs["담당자 이메일"] || "",
            },
          ]
        }

        console.log("Transformed data:", transformedData)

        if (transformedData.length === 0) {
          console.warn("⚠️ 데이터가 비어있습니다!")
        } else {
          transformedData.forEach((record, index) => {
            const emptyFields = []
            if (!record.company) emptyFields.push("회사명")
            if (!record.amount) emptyFields.push("최종금액")
            if (!record.contactName) emptyFields.push("담당자")
            if (!record.contactEmail) emptyFields.push("담당자 이메일")

            if (emptyFields.length > 0) {
              console.warn(`⚠️ 레코드 ${index + 1}의 비어있는 필드:`, emptyFields.join(", "))
            }
          })
        }

        setRecords(transformedData)
      } catch (error) {
        console.error("Error fetching records:", error)
        alert("데이터를 불러오는데 실패했습니다.")
      } finally {
        setIsLoading(false)
      }
    }

    fetchRecords()
  }, [])

  const toggleRecord = (id: string) => {
    setSelectedRecords((prev) => (prev.includes(id) ? prev.filter((recordId) => recordId !== id) : [...prev, id]))
  }

  const toggleAll = () => {
    if (selectedRecords.length === records.length) {
      setSelectedRecords([])
    } else {
      setSelectedRecords(records.map((r) => r.id))
    }
  }

  const handlePreviewInvoices = () => {
    if (selectedRecords.length === 0) {
      alert("미리보기할 계약을 선택해주세요.")
      return
    }

    const selectedData = records.filter((record) => selectedRecords.includes(record.id))
    setPreviewRecords(selectedData)
    setPreviewOpen(true)
  }

  const handleGenerateInvoices = async () => {
    if (selectedRecords.length === 0) {
      alert("인보이스를 생성할 계약을 선택해주세요.")
      return
    }

    setIsGenerating(true)

    try {
      const selectedData = records.filter((record) => selectedRecords.includes(record.id))

      await generateInvoicePDF(selectedData)

      setSelectedRecords([])
    } catch (error) {
      console.error("Invoice generation failed:", error)
      alert("인보이스 생성 중 오류가 발생했습니다.")
    } finally {
      setIsGenerating(false)
    }
  }

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("ko-KR").format(amount) + "원"
  }

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-purple-50 via-white to-purple-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto mb-4"></div>
          <p className="text-slate-600">데이터 로딩중...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-50 via-white to-purple-50 p-6 md:p-12 relative">
      <div className="mx-auto max-w-7xl">
        <Card className="shadow-lg border-purple-100">
          <CardHeader className="border-b bg-white">
            <div className="flex items-center justify-between">
              <div>
                <CardTitle className="text-2xl flex items-center gap-2">
                  <FileText className="h-6 w-6 text-primary" />
                  Invoice Records
                </CardTitle>
                <CardDescription className="mt-2">
                  {selectedRecords.length}개 선택됨 · 총 {records.length}개 계약
                </CardDescription>
              </div>
              <div className="flex gap-3">
                <Button
                  onClick={handlePreviewInvoices}
                  disabled={selectedRecords.length === 0}
                  size="lg"
                  variant="outline"
                  className="border-primary text-primary hover:bg-primary/10 bg-transparent"
                >
                  <Eye className="mr-2 h-5 w-5" />
                  미리보기
                </Button>
                <Button
                  onClick={handleGenerateInvoices}
                  disabled={selectedRecords.length === 0 || isGenerating}
                  size="lg"
                  className="bg-primary hover:bg-primary/90 text-primary-foreground shadow-md"
                >
                  <Download className="mr-2 h-5 w-5" />
                  {isGenerating ? "생성중..." : "인보이스 만들기"}
                </Button>
              </div>
            </div>
          </CardHeader>
          <CardContent className="p-0">
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow className="bg-purple-50/50 hover:bg-purple-50/50">
                    <TableHead className="w-12">
                      <Checkbox
                        checked={selectedRecords.length === records.length && records.length > 0}
                        onCheckedChange={toggleAll}
                        aria-label="Select all"
                      />
                    </TableHead>
                    <TableHead className="font-semibold">회사명</TableHead>
                    <TableHead className="font-semibold">최종금액</TableHead>
                    <TableHead className="font-semibold">담당자</TableHead>
                    <TableHead className="font-semibold">담당자 이메일</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {records.length === 0 ? (
                    <TableRow>
                      <TableCell colSpan={5} className="text-center py-8 text-slate-500">
                        등록된 계약이 없습니다.
                      </TableCell>
                    </TableRow>
                  ) : (
                    records.map((record) => (
                      <TableRow
                        key={record.id}
                        className={`cursor-pointer transition-colors ${
                          selectedRecords.includes(record.id) ? "bg-purple-50 hover:bg-purple-100" : "hover:bg-slate-50"
                        }`}
                        onClick={() => toggleRecord(record.id)}
                      >
                        <TableCell>
                          <Checkbox
                            checked={selectedRecords.includes(record.id)}
                            onCheckedChange={() => toggleRecord(record.id)}
                            aria-label={`Select ${record.company}`}
                          />
                        </TableCell>
                        <TableCell className="font-medium text-slate-900">{record.company}</TableCell>
                        <TableCell className="font-semibold text-primary">{formatCurrency(record.amount)}</TableCell>
                        <TableCell className="text-slate-700">{record.contactName}</TableCell>
                        <TableCell className="text-slate-600">{record.contactEmail}</TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
            </div>
          </CardContent>
        </Card>
      </div>

      <InvoicePreviewDialog
        open={previewOpen}
        onOpenChange={setPreviewOpen}
        records={previewRecords}
        onDownload={handleGenerateInvoices}
      />

      <footer className="fixed bottom-0 left-0 right-0 flex items-center justify-between px-6 md:px-12 py-4 bg-transparent pointer-events-none">
        <div className="text-sm text-slate-400 pointer-events-auto">© 2025 MISO. All rights reserved.</div>
        <div className="pointer-events-auto">
          <Image src="/miso-logo.png" alt="MISO" width={120} height={40} className="h-8 w-auto opacity-90" />
        </div>
      </footer>
    </div>
  )
}
