"use client"

interface InvoiceData {
  id: string
  company: string
  amount: number
  contactName: string
  contactEmail: string
}

interface InvoiceTemplateProps {
  record: InvoiceData
  pageNumber?: number
  totalPages?: number
}

const COMPANY_INFO = {
  name: "(주)GS",
  address: "서울특별시 강남구 논현로 508 GS타워",
  bank: "우리은행 982-018207-01-002 ㈜지에스",
}

function formatCurrency(amount: number): string {
  return new Intl.NumberFormat("ko-KR", {
    style: "currency",
    currency: "KRW",
  }).format(amount)
}

export function InvoiceTemplate({ record, pageNumber, totalPages }: InvoiceTemplateProps) {
  const today = new Date().toLocaleDateString("ko-KR", {
    year: "numeric",
    month: "long",
    day: "numeric",
  })

  return (
    <div
      id={`invoice-${record.id}`}
      style={{
        width: "794px",
        minHeight: "1123px",
        padding: "75px",
        margin: "0 auto",
        boxSizing: "border-box",
        fontFamily: "'Malgun Gothic', 'Apple SD Gothic Neo', sans-serif",
        display: "flex",
        flexDirection: "column",
        backgroundColor: "#ffffff",
        color: "#000000",
      }}
    >
      {/* Header */}
      <div style={{ marginBottom: "56px" }}>
        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "flex-start", marginBottom: "38px" }}>
          <h1
            style={{
              fontSize: "32px",
              fontWeight: "bold",
              margin: 0,
              textAlign: "center",
              flex: 1,
            }}
          >
            Invoice / Geolae Myeongse Seo
          </h1>
          <div style={{ fontSize: "13px", textAlign: "right" }}>
            <div style={{ fontWeight: "bold" }}>Date:</div>
            <div>{today}</div>
          </div>
        </div>
        <div
          style={{
            borderTop: "2px solid #5B4FFF",
            marginTop: "38px",
          }}
        />
      </div>

      {/* From and To Section */}
      <div style={{ display: "flex", justifyContent: "space-between", marginBottom: "150px", marginTop: "56px" }}>
        <div style={{ width: "45%" }}>
          <div style={{ fontSize: "16px", fontWeight: "bold", marginBottom: "26px" }}>From:</div>
          <div style={{ fontSize: "13px", lineHeight: "1.5" }}>
            <div style={{ marginBottom: "19px" }}>{COMPANY_INFO.name}</div>
            <div style={{ marginBottom: "19px" }}>{COMPANY_INFO.address}</div>
            <div>Bank: {COMPANY_INFO.bank}</div>
          </div>
        </div>
        <div style={{ width: "45%", textAlign: "right" }}>
          <div style={{ fontSize: "16px", fontWeight: "bold", marginBottom: "26px" }}>To:</div>
          <div style={{ fontSize: "13px", lineHeight: "1.5" }}>
            <div style={{ marginBottom: "19px" }}>{record.company}</div>
            <div style={{ marginBottom: "19px" }}>Contact: {record.contactName}</div>
            <div>{record.contactEmail}</div>
          </div>
        </div>
      </div>

      {/* Table */}
      <div style={{ marginBottom: "94px" }}>
        {/* Table Header */}
        <div
          style={{
            backgroundColor: "#F1F5F9",
            padding: "38px 19px",
            border: "1px solid #DDDDDD",
            display: "flex",
            justifyContent: "space-between",
            fontSize: "13px",
            fontWeight: "bold",
          }}
        >
          <div>Description</div>
          <div>Amount</div>
        </div>
        {/* Table Row */}
        <div
          style={{
            padding: "56px 19px",
            border: "1px solid #DDDDDD",
            borderTop: "none",
            display: "flex",
            justifyContent: "space-between",
            fontSize: "13px",
          }}
        >
          <div>Service Fee</div>
          <div>{formatCurrency(record.amount)}</div>
        </div>
      </div>

      {/* Total */}
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          marginBottom: "113px",
          fontSize: "19px",
        }}
      >
        <div style={{ fontWeight: "bold" }}>Total:</div>
        <div style={{ fontWeight: "bold", color: "#5B4FFF" }}>{formatCurrency(record.amount)}</div>
      </div>

      {/* Footer */}
      <div
        style={{
          marginTop: "auto",
          paddingTop: "113px",
          fontSize: "12px",
          color: "#646464",
          lineHeight: "1.5",
        }}
      >
        <div style={{ marginBottom: "19px" }}>Thank you for your business.</div>
        <div>Payment due within 30 days from issue date.</div>
      </div>

      {/* Page Number */}
      {pageNumber !== undefined && totalPages !== undefined && (
        <div
          style={{
            marginTop: "38px",
            textAlign: "center",
            fontSize: "11px",
            color: "#646464",
          }}
        >
          {pageNumber} / {totalPages}
        </div>
      )}
    </div>
  )
}

