"use client"

import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Download, X, ZoomIn, ZoomOut } from "lucide-react"
import { ScrollArea } from "@/components/ui/scroll-area"
import { InvoiceTemplate } from "@/components/invoice-template"
import { useState, useEffect, useRef } from "react"

interface Record {
  id: string
  company: string
  amount: number
  contactName: string
  contactEmail: string
}

interface InvoicePreviewDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  records: Record[]
  onDownload: () => void
}

export function InvoicePreviewDialog({ open, onOpenChange, records, onDownload }: InvoicePreviewDialogProps) {
  const [zoom, setZoom] = useState(100) // 100% = fit to container
  const containerRef = useRef<HTMLDivElement>(null)
  const [containerWidth, setContainerWidth] = useState(0)

  useEffect(() => {
    if (open && containerRef.current) {
      // 컨테이너 너비 감지
      const updateWidth = () => {
        if (containerRef.current) {
          setContainerWidth(containerRef.current.offsetWidth)
        }
      }

      updateWidth()
      window.addEventListener("resize", updateWidth)

      return () => window.removeEventListener("resize", updateWidth)
    }
  }, [open])

  const handleDownloadClick = () => {
    onDownload()
    onOpenChange(false)
  }

  const handleZoomIn = () => {
    setZoom((prev) => Math.min(prev + 10, 200)) // 최대 200%
  }

  const handleZoomOut = () => {
    setZoom((prev) => Math.max(prev - 10, 50)) // 최소 50%
  }

  // A4 크기: 210mm x 297mm (at 96 DPI: 794px x 1123px)
  const a4WidthPx = 794
  const a4HeightPx = 1123

  // 컨테이너에 맞춰 기본 스케일 계산
  const baseScale = containerWidth > 0 ? (containerWidth - 80) / a4WidthPx : 0.6
  const finalScale = baseScale * (zoom / 100)

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-[90vw] w-full max-h-[95vh] p-0">
        <DialogHeader className="px-6 pt-6 pb-4 border-b">
          <div className="flex items-center justify-between">
            <DialogTitle className="text-2xl">인보이스 미리보기</DialogTitle>
            <div className="flex items-center gap-2">
              <div className="flex items-center gap-1 border rounded-md">
                <Button variant="ghost" size="icon" onClick={handleZoomOut}>
                  <ZoomOut className="h-4 w-4" />
                </Button>
                <span className="text-sm px-2 min-w-[60px] text-center">{zoom}%</span>
                <Button variant="ghost" size="icon" onClick={handleZoomIn}>
                  <ZoomIn className="h-4 w-4" />
                </Button>
              </div>
              <Button variant="ghost" size="icon" onClick={() => onOpenChange(false)}>
                <X className="h-5 w-5" />
              </Button>
            </div>
          </div>
          <p className="text-sm text-muted-foreground mt-2">총 {records.length}개의 인보이스가 생성됩니다</p>
        </DialogHeader>

        <ScrollArea className="h-[calc(95vh-180px)]">
          <div
            ref={containerRef}
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              padding: "2rem",
              gap: "2rem",
            }}
          >
            {records.map((record, index) => (
              <div
                key={record.id}
                style={{
                  width: `${a4WidthPx}px`,
                  transform: `scale(${finalScale})`,
                  transformOrigin: "top center",
                  marginBottom: index < records.length - 1 ? `${a4HeightPx * finalScale - a4HeightPx}px` : 0,
                  boxShadow: "0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)",
                  borderRadius: "8px",
                  overflow: "hidden",
                  position: "relative",
                }}
              >
                <InvoiceTemplate
                  record={record}
                  pageNumber={index + 1}
                  totalPages={records.length}
                />
              </div>
            ))}
          </div>
        </ScrollArea>

        <DialogFooter className="px-6 py-4 border-t bg-slate-50">
          <Button variant="outline" onClick={() => onOpenChange(false)}>
            취소
          </Button>
          <Button onClick={handleDownloadClick} className="bg-primary hover:bg-primary/90">
            <Download className="mr-2 h-4 w-4" />
            PDF 다운로드
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  )
}
